import {Route, Switch} from "react-router-dom";
import React from "react";
import PatchtestResultList from "./PatchtestResultList";
import PatchtestAgentInfo from "./PatchtestAgentInfo";
import PatchscanResultList from "./PatchscanResultList";
import PatchtestEventList from "./PatchtestEventList";
import PatchtestList from "./PatchtestList";
import PatchTriggerScheduleInfo from "./PatchTriggerScheduleInfo";

function Home() {
    return <div>Home</div>
}

function Dashboard() {
    return <div>Dashboard</div>
}

function ActiveUsers() {
    return <div>ActiveUsers</div>
}

function DisabledUsers() {
    return <div>DisabledUsers</div>
}
function Content() {
    return(
        <div>
            <Switch>
                <Route exact path="/home" component={Home} />
                <Route path="/dashboard" component={Dashboard} />
                <Route path="/patchtestAgentInfo" component={PatchtestAgentInfo} />
                <Route path="/patchtestResult" component={PatchtestResultList} />
                <Route path="/patchtestEvents" component={PatchtestEventList} />
                <Route path="/patchscanResult" component={PatchscanResultList} />
                <Route path="/patchtestRegister" component={PatchtestList} />
                <Route path="/patchscheduleRegister" component={PatchTriggerScheduleInfo} />
                <Route path="/profile" component={() => <div>Profile</div>} />
            </Switch>
        </div>
    );
}

export default Content;