import {useHistory} from "react-router-dom";
import {Menu} from "antd";
import {
    DashboardOutlined,
    HomeOutlined,
    PoweroffOutlined,
    UnorderedListOutlined,
    UserOutlined
} from "@ant-design/icons/lib/icons";
import React from "react";
import Axios from "axios";
import {useAppContext} from "../store";

function MainSideMenu() {
    const { store: { jwtToken }} = useAppContext()
    const history = useHistory();

    return (
        <div style={{ display: "flex", flexDirection: "row"}}>
            <Menu
                onClick = { ({key}) => {
                    if (key === "signout") {
                        // TODO: sign out logic here (Async 처리 고려하기)
                        async function postToURL() {
                            try {
                                const apiUrl = "/rest-auth/logout/"
                                const headers = { Authorization: `JWT ${jwtToken}` };
                                const data = {};
                                const response = await Axios.post(apiUrl, data, { headers });

                                console.log("response : ", response);
                                history.push("/");
                            } catch (error) {
                                if (error.response) {
                                    // TODO: 응답 정보 확인 후 아래 응답 필드 수정 필요
                                    const {data: errorFieldsMessage} = error.response;
                                    console.log("error Response : ", errorFieldsMessage);
                                }
                            }
                        }
                        postToURL();
                    } else {
                        history.push(key);
                    }
                }}
                defaultSelectedKeys={[window.location.pathname]}
                items = {[
                    // {label: "Home", key: "/home", icon: <HomeOutlined />},
                    // {label: "Dashboard", key: "/dashboard", icon: <DashboardOutlined />},
                    {
                        label: "Patch Test",
                        // key: "/usersList",
                        icon: <UnorderedListOutlined />,
                        children: [
                            {
                                label: "Agent Info", key: "/patchtestAgentInfo",
                            },
                            {
                                label: "Patch Test Result", key: "/patchtestResult",
                            },
                            {
                                label: "Patch Test Events", key: "/patchtestEvents",
                            },
                        ]
                    },
                    {
                        label: "Patch Scan",
                        // key: "/usersList",
                        icon: <UnorderedListOutlined />,
                        children: [
                            {
                                label: "Patch Scan Result", key: "/patchscanResult",
                            },
                        ]
                    },
                    {
                        label: "기준정보",
                        // key: "/usersList",
                        icon: <UnorderedListOutlined />,
                        children: [
                            {
                                label: "패치항목 등록", key: "/patchtestRegister",
                            },
                            {
                                label: "Agent 스케줄 등록", key: "/patchscheduleRegister",
                            },
                        ]
                    },
                    {label: "Profile", key: "/profile", icon: <UserOutlined />},
                    {label: "Signout", key: "signout", icon: <PoweroffOutlined />, danger: true },
                ]}>
            </Menu>
        </div>
    );
}

export default MainSideMenu;