        console.log("================ Monitoring ================");

        async function postToURL() {
            const headers = {Authorization: `JWT ${jwtToken}`};
            try {
                const response = await axios.get(apiUrl, {headers});
                console.log("loaded response : ", response);

                const {data} = response;
                setPostList(data);

            } catch (error) {
                if (error.response) {
                    const {data: errorFieldsMessage} = error.response;
                    console.log("error Response : ", errorFieldsMessage);
                    // errorFieldsMessage 데이터 형태 => { username: ["m1", "m2"], password : [] }
                    // 수정할 결과 => { username: "m1, m2", password : "" }
                    setFieldsErrors(
                        Object.entries(errorFieldsMessage).reduce((accumulator, [fieldName, errors]) => {
                                // errors의 수정할 데이터 형태 : ["m1", "m2"].join(" ") => "m1 m2"
                                accumulator[fieldName] = {
                                    validateStatus: "error",
                                    help: errors.join(" "),
                                }
                                return accumulator;
                            },
                            {}
                        )
                    );
                }
            }
        }

        postToURL();
        console.log("mounted");
