import React from "react";
import {Redirect, Route} from "react-router-dom"
import { useAppContext } from "../store";

function LoginRequiredRoute({ component: Component, ...kwargs }) {
    const { store: { isAuthenticated }} = useAppContext();

    // if ( isAuthenticated ) {
    //     return <Route {...kwargs} components={component} />;
    // } else {
    //     return <Redirect />
    // }

    // props : matchLocation 정보 등이 넘어옴
    return <Route {...kwargs} render={props => {
        if (isAuthenticated) {
            return <Component {...props} />
        }
        else {
            return <Redirect to={{ pathname: "/accounts/login", state: { from: props.location } }} />
        }

    }} />;
}

export default LoginRequiredRoute