import React, {createContext, useContext, useReducer} from "react";
// import useLocalStorage from "./utils/useLocalStorage";
import { getStorageItem, setStorageItem } from "./utils/useLocalStorage"
import useReducerWithSideEffects, { UpdateWithSideEffect, Update } from "use-reducer-with-side-effects";

// const initialState = {
//     jwtToken: "",
// }

// const AppContext = createContext(initialState);
const AppContext = createContext();
const reducer = (prevState, action) => {
    const { type } = action;

    if (type === SET_TOKEN) {
        const { payload: jwtToken } = action;
        const newState = { ...prevState, jwtToken, isAuthenticated: true };
        return UpdateWithSideEffect(newState, (state, dispatch) => {
            setStorageItem("jwtToken", jwtToken);
        });
    } else if (type === DELETE_TOKEN) {
        const newState = { ...prevState, jwtToken: "", isAuthenticated: false};
        return UpdateWithSideEffect(newState, (state, dispatch) => {
            setStorageItem("jwtToken", "");
        });
    }
    return prevState;
}
const AppProvider = ( {children} ) => {

    // const [store, dispatch] = useReducer(reducer, initialState, () => (
    //     {
    //         jwtToken: useLocalStorage("jwtToken", "")
    //     }));

    // getStorageItem 함수로 분리 이후
    // const [store, dispatch] = useReducer(reducer, null, () => (
    //     {
    //         jwtToken: getStorageItem("jwtToken", "")
    //     }));
    // return (
    //     <AppContext.Provider value={{ store, dispatch }}>
    //         {children}
    //     </AppContext.Provider>
    // );

    // use-reducer-with-side-effects 라이브러리 활용방법
    const jwtToken = getStorageItem("jwtToken", "");
    const [store, dispatch] = useReducerWithSideEffects(reducer, {
        jwtToken,
        isAuthenticated: jwtToken.length > 0,
    });
    return (
        <AppContext.Provider value={{ store, dispatch }}>
            {children}
        </AppContext.Provider>
    );
};

export default AppProvider

// 별도 Hook 생성하여 사용
export const useAppContext = () => useContext(AppContext);

// Actions
const SET_TOKEN = "APP/SET_TOKEN";
const DELETE_TOKEN = "APP/DELETE_TOKEN";

export const setToken = token => ({ type: SET_TOKEN, payload: token});
export const deleteToken = () => ({ type: DELETE_TOKEN });