import React, { useEffect, useState } from "react";
import { Card, Form, Input, Button, notification } from "antd";
import { SmileOutlined, FrownOutlined } from "@ant-design/icons"
import {useHistory, useLocation} from "react-router-dom";
import Axios from "axios";
import useLocalStorage from "../../utils/useLocalStorage";
import {setToken, useAppContext} from "../../store";

function Login() {
    const history = useHistory();
    const location = useLocation();

    const { store, dispatch } = useAppContext();

    const [fieldsErrors, setFieldsErrors] = useState({});

    const { from: LoginRedirectUrl } = location.state || { from: { pathname: "/dashboard" }};
    const onFinish = (values) => {
        async function postToURL() {
            const { username, password } = values;

            setFieldsErrors({});

            const data = { username, password };
            try {
                const response = await Axios.post("/rest-auth/login/", data);
                console.log("response : ", response);

                const { data: { token: jwtToken } } = response;

                console.log("jwtToken : ", jwtToken);

                // 1) context API 활용하는 방법
                dispatch(setToken(jwtToken));
                // 2) 외부 라이브러리 활용하는 방법
                // setJwtToken(jwtToken);

                notification.open({
                    message: "로그인 성공",
                    icon: <SmileOutlined style={{ color: "#108ee9" }} />
                });

                history.push(LoginRedirectUrl);
            } catch (error) {
                if (error.response) {
                    const { data: errorFieldsMessage } = error.response;
                    console.log("error Response : ", errorFieldsMessage);
                    // errorFieldsMessage 데이터 형태 => { username: ["m1", "m2"], password : [] }
                    // 수정할 결과 => { username: "m1, m2", password : "" }
                    setFieldsErrors(
                        Object.entries(errorFieldsMessage).reduce((accumulator, [fieldName, errors]) => {
                                // errors의 수정할 데이터 형태 : ["m1", "m2"].join(" ") => "m1 m2"
                                accumulator[fieldName] = {
                                    validateStatus: "error",
                                    help: errors.join(" "),
                                }
                                return accumulator;
                            },
                            {}
                        )
                    );
                    notification.open({
                        message: "로그인 실패",
                        description: "ID 혹은 Password를 다시 확인해 주세요.",
                        icon: <FrownOutlined style={{ color: "#ff3333" }} />
                    });
                }
            }
        }
        postToURL();
    }

    return (
        <div
            style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                height: "100vh",
            }}
        >
            <Card title="로그인">
                <Form
                    labelCol={{
                        span: 8,
                    }}
                    wrapperCol={{
                        span: 16,
                    }}
                    style={{
                        maxWidth: 600,
                    }}
                    onFinish={onFinish}
                    // onFinishFailed={onFinishFailed}
                    autoComplete="off"
                >
                    <Form.Item
                        label="Username"
                        name="username"
                        rules={[
                            {
                                required: true,
                                message: 'Please input your username!',
                            },
                        ]}
                        hasFeedback
                        {...fieldsErrors.username}
                        {...fieldsErrors.non_field_errors}
                    >
                        <Input />
                    </Form.Item>

                    <Form.Item
                        label="Password"
                        name="password"
                        rules={[
                            {
                                required: true,
                                message: 'Please input your password!',
                            },
                        ]}
                        hasFeedback
                        {...fieldsErrors.password}
                    >
                        <Input.Password />
                    </Form.Item>

                    <Form.Item
                        wrapperCol={{
                            offset: 8,
                            span: 16,
                        }}
                    >
                        <Button type="primary" htmlType="submit">
                            Submit
                        </Button>
                    </Form.Item>
                </Form>
            </Card>
        </div>
    );
}

export default Login;